<template>
  <div>
    <div class="title">
        客户历史投诉/解决方案
    </div>
    <div class="conterBox">
        <!-- 查询搜索框 -->
        <el-form :inline="true" :model="formInline" class="demo-form-inline">
            <el-form-item label="公司名称">
                <el-input v-model="formInline.user" placeholder="请输入公司名称"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="onSubmit">查询</el-button>
            </el-form-item>
        </el-form>
        <!-- 表格列表与操作 -->
        <el-table
          :data="tableData"
          border
          style="width: 100%">
          <el-table-column
            prop="date"
            label="日期">
          </el-table-column>
          <el-table-column
            prop="name"
            label="姓名">
          </el-table-column>
          <el-table-column
            prop="address"
            label="地址">
          </el-table-column>
          <el-table-column
            label="操作">
            <template slot-scope="scope">
              <el-button @click="handleClick(scope.row)" type="text" size="small">查看</el-button>
            </template>
          </el-table-column>
        </el-table>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        formInline: {//搜索框
          user: ''
        },
        tableData: [{//表格循环文件
            date: '2016-05-02',
            name: '王小虎',
            address: '上海市普陀区金沙江路 1518 弄'
            }, {
            date: '2016-05-04',
            name: '王小虎',
            address: '上海市普陀区金沙江路 1517 弄'
            }, {
            date: '2016-05-01',
            name: '王小虎',
            address: '上海市普陀区金沙江路 1519 弄'
            }, {
            date: '2016-05-03',
            name: '王小虎',
            address: '上海市普陀区金沙江路 1516 弄'
        }]
      }
    },
    methods: {
      onSubmit() {//点击查询按钮触发的事件
        console.log('submit!点击查询按钮触发的事件');
      },
      handleClick(i){//表格-操作-查看触发事件 i表格中的哪一行
        this.$router.push('/bkIndex/bkCASsingle')
        console.log('表格-操作-查看触发事件')
      }
    }
  }
</script>

<style lang="stylus" scoped>
.title
    font-size 20px
    color #333
    margin-bottom 20px
    margin-top 20px
    padding-left 15px
    border-left 3px solid #409EFF
.conterBox
    padding 30px
</style>


